.loader {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  height: 40px;
  width: 80px;
  box-sizing: border-box;
}
.loader .loaderItem {
  position: relative;
  float: left;
  height: 40px;
  width: 4px;
  margin: 0 2px;
  background-color: #ffffff;
}
.loader .loaderItem:nth-child(1) {
  -webkit-animation: loaderItem-1 2s linear infinite;
          animation: loaderItem-1 2s linear infinite;
}
.loader .loaderItem:nth-child(2) {
  -webkit-animation: loaderItem-2 2s linear infinite;
          animation: loaderItem-2 2s linear infinite;
}
.loader .loaderItem:nth-child(3) {
  -webkit-animation: loaderItem-3 2s linear infinite;
          animation: loaderItem-3 2s linear infinite;
}
