}
.loader .loaderItem:nth-child(10) {
  -webkit-animation: loaderItem-10 2s linear infinite;
          animation: loaderItem-10 2s linear infinite;
}
.loader:after {
  content: "Loading...";
  font-size: 16px;
  font-family: "Arial";
  color: #ffffff;
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  bottom: -32px;
  margin: auto;
}
@-webkit-keyframes loaderItem-1 {
  1% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  11% {
    -webkit-transform: scaleY(1.4);
            transform: scaleY(1.4);
  }
  21% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}
